.banner
    position: relative
    background-color: $tc-blue
    > .container
        display: flex
        justify-content: center
        align-items: center
        @media (max-width: 576px)
            justify-content: start
            padding-left: 1rem
    .banner-wrapper
        padding: 15px 10px 15px 5px
    .banner-text
        position: relative
        display: flex
        column-gap: 5px
        *
            font-size: 1.2rem
            color: $tc-white
        @media (max-width: 576px)
            // margin: 5px auto
            // text-align: center
            // max-width: 90%
            .meta-data
                display: block
            *
                font-size: 1.1rem
        .title
            font-weight: bolder

    .banner-close
        display: inline-flex
        cursor: pointer
        position: absolute
        right: 15px
        top: 50%
        transform: translateY(-50%)
        *
            color: white

    .tooltip
        position: absolute
        transform-origin: top center
        transform: translateX(-50%) scale(0.2)
        left: 50%
        top: 30px
        z-index: -1
        transition: transform .2s linear
        width: 100%
        &.show
            z-index: 1080
            transform: translateX(-50%) scale(1)
        .tooltip-arrow
            left: calc(50% - 0.4rem)
        .tooltip-inner
            max-width: unset
